#cart table{
    width: 100%;
    table-layout: fixed;
    white-space: nowrap;
    border-collapse: collapse;
}
a{
    text-decoration: none;
}
#cart table td:nth-child(1){
    width: 100px;
    text-align: center;
}
#cart table td:nth-child(2){
    width: 150px;
    text-align: center;
}
#cart table td:nth-child(3){
    width: 250px;
    text-align: center;
}
#cart table td:nth-child(4),#cart table td:nth-child(5),#cart table td:nth-child(6){
    width: 150px;
    text-align: center;
}
#cart table td:nth-child(5) input{
    width: 70px;
    padding: 10px 5px 10px 15px;
}
#cart table thead{
    border: 1px solid #e1e1e1;
    border-right: none;
    border-left: none;
}
#page-header h2,p{
    color: #fff;
}
#page-header{
    background-image: url(../Home/img/b1.jpg);
    height: 40vh;
    width: 100%;
    background-size: cover;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    text-align: center;
    padding: 14px;
    }
#cart{
    overflow: auto;
}
#cart table td{
    font-size: 13px;
    font-weight: 700;
    text-transform: uppercase;
    padding: 18px 0;
}
#cart table tbody tr td {
     padding-top: 15px;
}
#cart table tbody  td{
    font-size: 13px;
}
#cart table a{
    cursor: pointer;
}
#cart table img{
    width: 70px;
}
#cart-add{
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
}
#coupon{
    width: 50%;
    margin-bottom: 30px;
}
#coupon h3,.sub-total h3{
    padding-bottom: 15px;

}
#coupon input{
    width: 60%;
    padding: 10px 20px;
    outline: none;
    margin-right: 10px;
    border: 1px solid #e1e1e1;
}
#coupon button,.sub-total button{
    background-color: #088178;
    color: #fff;
    padding: 12px 20px;
}
.sub-total table{
    width: 100%;
    border-collapse: collapse;
    margin-bottom: 20px;
}
.sub-total td{
    width: 50%;
    border: 1px solid #e2e9e1;
    padding: 10px;
    font-size: 13px;
}

.sub-total{
    width: 50%;
    margin-bottom: 30px;
    padding: 30px;
    border: 1px solid #e2e9e1;

}