* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.body {
  width: 100%;
  min-height: 100vh;
  
}

.body {
  display: flex;
  font-family: "Open Sans", sans-serif;
}

.image {
  background: url(https://images.unsplash.com/photo-1559056961-1f4dbbf9d36a?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxzZWFyY2h8NDB8fGxvZ2lufGVufDB8fDB8fA%3D%3D&auto=format&fit=crop&w=500&q=60)
    rgba(255, 255, 255, 0.3);
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  background-blend-mode: lighten;
  flex: 1 1;
}

.sign-in {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  flex: 1 1;
}

h1 {
  font-size: 3.5rem;
  margin-bottom: 30px;
}

form {
  display: flex;
  flex-direction: column;
  grid-gap: 20px;
  gap: 20px;
}

.input[type="email"],
.input[type="password"] {
  padding: 10px;
  font-size: 1.2rem;
  border: none;
  border-bottom: solid 1px #000;
  outline: none;
}

.input[type="email"]:-ms-input-placeholder, .input[type="password"]:-ms-input-placeholder {
  letter-spacing: 2px;
  -ms-transition: all 0.3s ease;
  transition: all 0.3s ease;
}

.input[type="email"]::placeholder,
.input[type="password"]::placeholder {
  letter-spacing: 2px;
  transition: all 0.3s ease;
}

.input[type="email"]:hover:-ms-input-placeholder, .input[type="password"]:hover:-ms-input-placeholder {
  color: #000;
}

.input[type="email"]:hover::placeholder,
.input[type="password"]:hover::placeholder {
  color: #000;
}

.input[type="submit"] {
  background-color: #000;
  color: #fff;
  padding: 10px;
  font-size: 1.3rem;
  cursor: pointer;
  border: none;
  transition: all 0.3s ease;
  border-radius: 15px;
}

.input[type="submit"]:hover {
  background-color: #eee;
  color: #000;
}

.a {
  color: #424242;
  text-decoration: none;
  align-self: center;
}

@media screen and (max-width: 786px) {
  .image {
    display: none;
  }
}

